body {
  font-family: "Inter", sans-serif;
  background-color: #FEF2F0;
}

.container {
  width: 1260px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: inherit;
}

.btn {
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}
.btn--middle {
  font-size: 14px;
  padding: 10px 23px;
  border-radius: 100px;
}
.btn--large {
  font-size: 18px;
  padding: 12px 40px;
  border-radius: 100px;
}
.btn--green {
  background-color: #808D54;
  border: 1px solid #445439;
  color: #FEF2F0;
  font-weight: 600;
}
.btn--white {
  background-color: #FEF2F0;
  border: 1px solid #445439;
  color: #445439;
  font-weight: 600;
  text-transform: uppercase;
}

.icon {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.icon--middle {
  font-size: 14px;
  width: 36px;
  height: 36px;
}
.icon--round {
  border-radius: 50%;
}
.icon--white {
  background-color: #FEF2F0;
  border: 1px solid #445439;
  color: #445439;
}
.icon--green {
  background-color: #808D54;
  border: 1px solid #445439;
  color: #FEF2F0;
}
.icon--telegram {
  padding-top: 1px;
  padding-right: 1px;
}

.site-header {
  height: 86px;
  padding: 19px 0;
  display: flex;
  justify-content: center;
}
.site-header__container {
  width: 100%;
  max-width: 1800px;
  margin: 0 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.site-header__nav ul {
  display: flex;
  gap: 50px;
  list-style: none;
}
.site-header__nav ul a {
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  color: #445439;
}
@media (max-width: 1400px) {
  .site-header__nav ul {
    gap: 30px;
  }
}
@media (max-width: 1200px) {
  .site-header__nav ul {
    gap: 24px;
  }
  .site-header__nav ul a {
    font-size: 12px;
  }
}
@media (max-width: 1000px) {
  .site-header__nav ul {
    display: none;
  }
}
.site-header__contacts {
  display: flex;
  gap: 7px;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1400px) {
  .site-header__contacts .icon--whatsapp, .site-header__contacts .icon--telegram {
    display: none;
  }
}
.site-header__language {
  font-size: 14px;
  color: #445439;
  margin-right: 16px;
}
@media (max-width: 1200px) {
  .site-header__language {
    display: none;
  }
}
.site-header__burger {
  display: none;
}
@media (max-width: 1000px) {
  .site-header__burger {
    display: block;
  }
}

.footer {
  height: 485px;
  color: #445439;
}
.footer__container {
  max-width: 1800px;
  height: 100%;
  margin: 0 auto;
  padding: 62px 0;
  display: flex;
  justify-content: space-between;
}
.footer__title {
  font-weight: 700;
  margin-bottom: 19px;
}
.footer__contacts {
  margin-left: 120px;
}
.footer__address {
  margin-top: 37px;
}
.footer__feedback {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer__social {
  display: flex;
  justify-content: left;
  gap: 8px;
}
.footer__links {
  font-size: 18px;
  display: flex;
  gap: 60px;
  justify-content: left;
  justify-content: space-between;
}

.hero {
  width: 100%;
  height: 824px;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero__container {
  padding-top: 164px;
  color: white;
}
.hero__header {
  max-width: 720px;
  margin-bottom: 26px;
  font-family: "Marcellus", Arial, Helvetica, sans-serif;
  font-size: 45px;
}
.hero__description {
  font-size: 25px;
  margin-bottom: 35px;
}
.hero__button {
  margin-bottom: 100px;
}

.article {
  padding: 134px 0;
  background-repeat: no-repeat;
  background-position: top;
}
.article__container {
  display: flex;
  gap: 95px;
  color: #445439;
}
.article__container .article__left, .article__container .article__right {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.article__title {
  font-size: 45px;
  margin-bottom: 44px;
  font-family: "Marcellus", Arial, Helvetica, sans-serif;
}
.article__content {
  font-size: 20px;
}
.article__slogan {
  font-size: 45px;
  margin-right: 50px;
  font-family: "Marcellus", Arial, Helvetica, sans-serif;
}

.features {
  background-color: #808D54;
  color: white;
}
.features__container {
  padding: 104px 0;
}

.whatwedo__container {
  padding: 120px 0;
}
.whatwedo__title {
  font-size: 45px;
  color: #445439;
  margin-bottom: 44px;
  font-family: "Marcellus", Arial, Helvetica, sans-serif;
}
.whatwedo__items {
  display: flex;
  flex-wrap: wrap;
  gap: 27px;
}
.whatwedo__item {
  height: 565px;
  width: 400px;
}
.whatwedo__item-image {
  background-color: #808D54;
  height: 468px;
  color: white;
  padding: 36px 160px 41px 45px;
  font-size: 18px;
}
.whatwedo__item-label {
  color: #445439;
  font-size: 18px;
  font-weight: 700;
  padding: 14px 0 11px 0;
  text-transform: uppercase;
  border-bottom: 1px solid #445439;
}

.blog {
  color: #445439;
}
.blog__container {
  padding-top: 64px;
}
.blog__title {
  font-size: 45px;
  font-family: "Marcellus", Arial, Helvetica, sans-serif;
}
.blog__items {
  margin-top: 44px;
  display: flex;
  flex-wrap: wrap;
  gap: 27px;
}
.blog__item {
  width: 400px;
  margin-bottom: 100px;
}
.blog__item-image {
  padding: 24px;
  height: 289px;
  background-color: #808D54;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 24px;
}
